.settings_modal_imageurl_wrapper {
    display: flex;
}

.settings_modal_remove_imageurl_icon,
.settings_modal_add_imageurl_icon {
    width: 42px;
    height: 42px;
    margin-left: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #000;
    transition: background .15s linear;
    cursor: pointer;
    background: #fff;
}

.settings_modal_add_imageurl_icon:hover {
    color: #fff;
    background: #000;
}

.settings_modal_remove_imageurl_icon:hover {
    color: #fff;
    background: #f00;
}

.settings_modal_text_editor {
    border: 2px solid black;
    margin-bottom: 24px;
}

.settings_modal_text_editor_content {
    padding: 12px;
    min-height: 8rem;
    font-size: 0.875rem;
    font-weight: 400;
}
